<template>
    <div class="bgColor">
        <div class="header">
            <img src="../../../images/Apply.png" alt="">
            <div class="curatorText">
                <img src="../../../images/curatorText03.png" alt="">
                <p class="mask-top">200元300张房卡</p>
            </div>
        </div>

        <div class="content">
            <p class="text">请选择购买数量</p>
            <div class="inject">
                <span class="reduce" @click="reduce"></span><span class="plus" @click="plus"></span><p class="totalNumber">{{totalNumber}}</p>
            </div>
            <p class="button-list">
                <span class="rechange" @click="_Apply">申请</span>
            </p>
        </div>
        <div class="footer-img">
            <img src="../../../images/gengduo.png" alt="">
        </div>
        <div class="bottom">
            <span>官方公众号： sli-qipai</span>
            <span>官方客服：400-0348-369</span>
        </div>

    </div>

</template>

<script>
// import footerText from '../../../components/footerText'
export default {
    components: {
        // footerText,
  },
  data() {
      return {
          totalNumber:100,
      }
  },
  methods:{
      _Apply:function(){
          this.$router.push({path:'/becomeCurator/paymentMethods'})
          //返回后台数据方法待添加
      },
      reduce:function(){
          this.totalNumber -= 1
          if(this.totalNumber<1){
              this.totalNumber = 1
          }
      },
      plus:function(){
           this.totalNumber += 1
           //一次购买最大值未设置
      }

  }
}
</script>

<style lang="less" scoped>
    .bgColor{
        height:100%;
        background:#EEEEEE;
        position: relative;
        #footer_text{
            position: absolute;
            bottom:0;
        }
    }
    .header{
        height: 7rem;
        background: #4CACFF;
        text-align: center;
        position: relative;
        img{
            width:84%;
            margin-top:14%;
        }
        .curatorText{
            position: absolute;
            width:84%;
            bottom:-21%;
            left:8%;
            margin:0 auto;
            text-align: center;
            img{
                width:100%;
                margin: 0;
            }
            .mask-top{
                width:100%;
                height:1.3rem;
                line-height:1.3rem;
                font-size:.8rem;
                font-weight: bolder;
                color:#EE143D;
                position: absolute;
                bottom:0.3em;
                background: url(../../../images/mask01.png) no-repeat ;
                background-size: 100% 100%;
            }
        }
    }
    .content{
        text-align: center;
        margin-top: 17%;
        .inject{
            width: 80%;
            line-height:1.2rem;
            margin: 0 auto;
            margin-bottom:5%;
            span{
                float:left;
                display: inline-block;
                width: 7%;
                height:1.2rem;
                // border:1px solid yellow;
                background: url(../../../images/reduce.png) no-repeat;
                background-size: 100%;
                background-position: center;
            }
            .plus{
                background: url(../../../images/plus.png) no-repeat;
                background-size: 100%;
                background-position: center;
                float:right;
            }
            p{
                margin: 0 auto;
                background: #fff;
                border:1px solid #CBCBCB;
                border-radius: 6px;
                width:73%;
                height:100%;
                color:#666;
                line-height: 1.2rem;
            }
        }
        .text{
            font-size:.4rem;
        }
        .button-list{
             width:84%;
             margin: 0 auto;
        }
         .rechange{
             width:100%;
             height:1.4rem;
             color:#fff;
             font-size:.5rem;
             line-height: 1.4rem;
             display: inline-block;
             background-color: #EE8F45;
             border-radius: 6px;
        }
        .insure{
            background: #5BA9ED;
        }
    }
    .text{
        text-align:center;
        color:#A2A2A2;
    }
    .footer-img{
        text-align: center;
        margin-top:4%;
        img{
            width:75%;
        }
    }
    .bottom{
        width:90%;
        position: absolute;
        bottom:2%;
        left:5%;
        color:#AEAEAE;
        span{
            display:inline-block;
            text-align: center;
            font-size:.3rem;
            width:50%;
            float:left;
        }
    }
</style>



