export default {
    components: {
        // footerText,
  },
  data() {
      return {
          totalNumber:100,
      }
  },
  methods:{
      _Apply:function(){
          this.$router.push({path:'/becomeCurator/paymentMethods'})
          //返回后台数据方法待添加
      },
      reduce:function(){
          this.totalNumber -= 1
          if(this.totalNumber<1){
              this.totalNumber = 1
          }
      },
      plus:function(){
           this.totalNumber += 1
           //一次购买最大值未设置
      }

  }
}



